<template>
  <div class="info-input">
    <div class="info-input__title">
      <p>Заполните данные</p>
    </div>
    <!-- <form action=""> -->
    <div class="info-input__form">
      <div class="info-input__container">
        <div class="select-block__item-block">
          <label for="standard-select">Выберите номинацию</label>
          <div class="select" :class="{ 'error-valid': $v.nominate.$error }">
            <select required id="standard-select" v-model="nominate">
              <option value="ig">ИЖ</option>
              <option value="im">ИМ</option>
              <option value="sp">СП</option>
              <option value="tr">ТР</option>
              <option value="gr">ГР</option>
              <option value="tg">ТГ</option>
              <option value="gp">ГП</option>
            </select>
            <span class="focus"></span>
          </div>
        </div>
        <div class="select-block__item-block">
          <label for="standard-select">Выберите возрастную категорию</label>
          <div class="select" :class="{ 'error-valid': $v.age.$error }">
            <select required id="standard-select" v-model="age">
              <option value="1">6-8</option>
              <option value="2">9-11</option>
              <option value="3">12-14</option>
              <option value="4">15-17</option>
              <option value="5">18+</option>
            </select>
            <span class="focus"></span>
          </div>
        </div>
        <div class="info__input-fio">
          <label for="name">Фамилия</label>
          <p class="error-input" v-if="!$v.name.alpha">
            *Вписывать одну фамилию
          </p>
          <input
            type="text"
            id="item"
            name="name"
            placeholder="Фамилия"
            v-model="name"
            :class="{ 'error-valid': $v.name.$error }"
            required
            aria-label="Вводите"
            class="input-search"
          />
        </div>
        <div class="info-input__srope">
          <div class="info__input">
            <label for="art">Средняя оценка ART</label>
            <input
              type="number"
              id="item"
              name="art"
              max="10"
              placeholder="10.0"
              v-model="art"
              required
              :class="{ 'error-valid': $v.art.$error }"
              aria-label="Вводите"
              class="input-search"
            />
          </div>
          <div class="info__input">
            <label for="exe">Средняя оценка EXE</label>
            <input
              type="number"
              id="item"
              name="exe"
              max="10"
              placeholder="10.0"
              v-model="exe"
              required
              :class="{ 'error-valid': $v.exe.$error }"
              aria-label="Вводите"
              class="input-search"
            />
          </div>
          <div class="info__input">
            <label for="dif">Средняя оценка DIF</label>
            <input
              type="number"
              id="item"
              max="10"
              name="dif"
              placeholder="10.0"
              :class="{ 'error-valid': $v.dif.$error }"
              v-model="dif"
              aria-label="Вводите"
              class="input-search"
            />
          </div>
          <div class="info__input">
            <label for="deduction">Общая сбавка</label>
            <input
              type="number"
              id="item"
              name="deduction"
              :class="{ 'error-valid': $v.deduction.$error }"
              placeholder="Сбавки"
              v-model="deduction"
              aria-label="Вводите"
              class="input-search"
            />
          </div>
        </div>
        <div class="info__input">
          <label for="score">ОБЩАЯ ОЦЕНКА</label>
          <input
            type="number"
            id="item"
            required
            name="score"
            max="30"
            :class="{ 'error-valid': $v.score.$error }"
            placeholder="30.0"
            v-model="score"
            aria-label="Вводите"
            class="input-search"
          />
        </div>
        <div class="info-input__button-block">
          <div class="info-input__btn">
            <input
              type="submit"
              value="Отправить оценку"
              class="button btn-primary"
              @click="onSubmit()"
            />
          </div>
          <div class="info-input__btn">
            <input
              type="button"
              value="Сбросить"
              class="button btn-primary"
              @click="deleteFilter()"
            />
          </div>
        </div>
      </div>
    </div>
    <!-- </form> -->
  </div>
</template>

<script>
import { required, helpers } from "vuelidate/lib/validators";
// import axios from "axios";
import { mapState } from "vuex";
import { actionTypes } from "@/store/modules/board";

export default {
  name: "AppInfoInput",
  data() {
    return {
      nominate: "",
      age: "",
      name: "",
      exe: "",
      art: "",
      dif: "",
      deduction: "",
      score: "",
      board: {},
    };
  },
  validations: {
    name: {
      required,
      alpha: helpers.regex("alpha", /^[А-Яа-яЁё]*$/),
    },
    nominate: {
      required,
      // alpha: helpers.regex("alpha", /^[А-Яа-яЁё]*$/),
    },
    age: {
      required,
    },
    exe: {
      required,
    },
    art: {
      required,
    },
    dif: {
      // required,
    },
    deduction: {},
    score: {
      required,
    },
  },
  computed: {
    ...mapState({
      scoreItem: (state) => state.board.scoreItem,
      validationErrors: (state) => state.board.validationErrors,
    }),
  },
  methods: {
    onSubmit() {
      if (this.$v.$invalid) {
        this.$v.$touch();
        console.log("error");
        // this.submitStatus = 'ERROR in first';
      } else {
        this.board = {
          nominate: this.nominate,
          age: this.age,
          name: this.name,
          art: this.art,
          exe: this.exe,
          dif: this.dif,
          deduction: this.deduction,
          score: this.score,
        };
        this.$store
          .dispatch(actionTypes.postScore, this.board)
          .then(() => {
            this.nominate = "";
            this.age = "";
            this.name = "";
            this.exe = "";
            this.art = "";
            this.dif = "";
            this.deduction = "";
            this.score = "";
          })
          .catch((er) => {
            console.log("Ошибка", er);
          });
      }
    },
    deleteFilter() {
      this.nominate = "";
      this.age = "";
      this.name = "";
      this.exe = "";
      this.art = "";
      this.dif = "";
      this.deduction = "";
      this.score = "";
    },
  },
  // },
};
</script>

