import { getItem, setItem } from "@/helpers/persistanceStorage";
import ScoreAPi from "@/api/score.js";
const state = {
  isLoading: false, //ожидание запроса
  validationErrors: null, //ошибки
  scoreItem: getItem("scoreItem") || "",
};

export const mutationTypes = {
  ScoreStart: "[score] ScoreStart",
  ScoreSuccess: "[score] ScoreSuccess",
  ScoreFailure: "[score] ScoreFailure",

  GetScoreStart: "[score] GetScoreStart",
  GetScoreSuccess: "[score] GetScoreSuccess",
  GetScoreFailure: "[score] GetScoreFailure",
};
export const actionTypes = {
  postScore: "[score] postScore",
  getScore: "[score] getScore",
};

const mutations = {
  [mutationTypes.ScoreStart](state) {
    state.isLoading = true;
  },
  [mutationTypes.ScoreSuccess](state) {
    state.isLoading = false;
    //  state.scoreItem = payload;
    //  setItem("scoreItem", state.scoreItem);
  },
  [mutationTypes.ScoreFailure](state, payload) {
    state.isLoading = false;
    state.validationErrors = payload;
  },
  [mutationTypes.GetScoreStart](state) {
    state.isLoading = true;
  },
  [mutationTypes.GetScoreSuccess](state, payload) {
    state.isLoading = false;
    state.scoreItem = payload;
  },
  [mutationTypes.GetScoreFailure](state, payload) {
    state.isLoading = false;
    state.validationErrors = payload;
  },
};
const actions = {
  [actionTypes.postScore](context, credentials) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.ScoreStart);
      ScoreAPi.postScore(credentials)
        .then((response) => {
          if (response.data.status === "SUCCESS") {
            // setItem("scoreItem", response.data);
            context.commit(mutationTypes.ScoreSuccess);
          } else {
            context.commit(mutationTypes.ScoreFailure, response);
          }
          resolve(response.data);
        })
        .catch((response) => {
          context.commit(mutationTypes.ScoreFailure, response);
          console.log("ошибка", response);
        });
    });
  },
  [actionTypes.getScore](context) {
    return new Promise((resolve) => {
      context.commit(mutationTypes.GetScoreStart);
      ScoreAPi.getScore()
        .then((response) => {
          if (response.data.status === "SUCCESS") {
            setItem("scoreItem", response.data.result);
            context.commit(mutationTypes.GetScoreSuccess, response.data.result);
          } else {
            context.commit(mutationTypes.GetScoreFailure, response);
          }
          resolve(response.data);
        })
        .catch((response) => {
          context.commit(mutationTypes.GetScoreFailure, response);
          console.log("ошибка", response);
        });
    });
  },
};

export default {
  state,
  mutations,
  actions,
};
